import React, { useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import IMAGES from "../../assets/images";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { FieldError } from "../../components/ErrorMessage/ErrorMessage";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../utils/path";
import { useDispatch, useSelector } from "react-redux";
import { createAccount } from "../../app/features/auth/auth.slice";
import { ClipLoader } from "react-spinners";

export default function SignIn() {
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);
  const [isChecked, setIsChecked] = useState(false);
  const { isLoading } = useSelector((state) => state.auth);
  const navigate = useNavigate();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const dispatch = useDispatch();

  const handleLinkClick = (path) => {
    navigate(path);
  };

  const handleCheckboxChange = (e) => {
    const { checked } = e.target;
    setIsChecked(checked);
  };

  function onSubmit(data) {
    const moveToNext = () => {
      navigate(PATH.OTPCODE, { state: data.email });
    };
    const finalData = {
      email: data.email,
      password: data.password,
    };

    dispatch(createAccount({ finalData, moveToNext }));
  }


  return (
    <div className="signin-component h-100">
      <Row className="vh-100 m-0">
        <Col sm={12} md={12} lg={7} className="p-0">
          <div className="bg-img">
            <div className="signin-page">
              <div
                style={{
                  padding: "22% 44% 22% 9%",
                }}
              >
                <h4 className="heading1">GP care with a Modern Touch.</h4>
                <p className="heading2">
                  Fast, hassle-free healthcare for the most common medical
                  conditions from the comfort of your home.
                </p>
              </div>
            </div>
          </div>
        </Col>
        <Col
          sm={12}
          md={12}
          lg={5}
          className="d-flex align-items-center py-4 h-100 bg-white"
        >
          <div className="signin-content">
            <div className="d-flex justify-content-center w-100 my-4">
              <img src={IMAGES.LOGO} alt="FAM Doc Logo" className="img-fluid" />
            </div>
            <Card className="border-0 bg-transparent signup-card-mainclass">
              <Card.Body>
                <Form className="p-3" onSubmit={handleSubmit(onSubmit)}>
                  <div className="signin-heading text-center">Sign Up</div>
                  <p className="text-center mb-5">
                    Please enter your details to create your account.
                  </p>
                  <Form.Group className="mb-3">
                    <Form.Label className="label-primary">Email</Form.Label>
                    <Form.Control
                      type="email"
                      name="email"
                      placeholder="Email"
                      size="lg"
                      {...register("email", {
                        required: true,
                        pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                      })}
                    />
                    {errors.email && (
                      <FieldError message={"This Field is Required"} />
                    )}
                  </Form.Group>
                  <Form.Group
                    className="position-relative"
                    controlId="formBasicPassword"
                  >
                    <Form.Label className="label-primary">Password</Form.Label>
                    <Form.Control
                      type={showPassword ? "text" : "password"}
                      placeholder="Password"
                      name="password"
                      size="lg"
                      {...register("password", { required: true })}
                    />
                    <div onClick={handleClickShowPassword} className="eye-icon">
                      {showPassword ? (
                        <AiOutlineEye size={18} />
                      ) : (
                        <AiOutlineEyeInvisible size={18} />
                      )}
                    </div>
                  </Form.Group>
                  {errors.password && (
                    <FieldError message={"This Field is Required"} />
                  )}
                  <Form.Group
                    className="my-3 d-flex"
                    controlId="formBasicCheckbox"
                  >
                    <Form.Check
                      type="checkbox"
                      className="card-internal-text me-2"
                      onChange={handleCheckboxChange}
                    />
                    <p style={{fontSize: '0.9rem'}}>
                      I agree with{" "}
                      <span
                        onClick={() => handleLinkClick(PATH.TERMSANDCONDITION)}
                        className="text-cursor-pointer policies-text text-decoration-underline"
                      >
                        Terms and Conditions
                      </span>{" "}
                      of use and{" "}
                      <span
                        onClick={() => handleLinkClick(PATH.PRIVACYPOLICY)}
                        className="text-cursor-pointer policies-text text-decoration-underline"
                      >
                        Privacy Policy
                      </span>
                    </p>
                  </Form.Group>
                  <Button
                    className={`w-100 ${isLoading && "disabled"}`}
                    type="submit"
                    disabled={!isChecked}
                  >
                    {isLoading === true ? (
                      <ClipLoader
                        color="white"
                        size={25}
                        className="d-flex m-auto"
                      />
                    ) : (
                      "Sign Up"
                    )}
                  </Button>
                </Form>
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </div>
  );
}
